@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
html,
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
  height: 100%;
}
.login-form {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px;
  margin: auto;
}
.container-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.timetable {
  display: flex;
  height: 80vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}
.loader {
  width: 30vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar {
  background-color: #343a40;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.dropdown {
  list-style: none;
  margin-right: 50px;
}
.dropdown a {
  color: #ffff;
  font-size: 18px;
}
.dropdown a:hover {
  color: #ffff;
}
.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
}
.nav-menu {
  background-color: #343a40;
  width: 250px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
  z-index: 100;
}
.nav-menu.active {
  left: 0;
  transition: 350ms;
}
.nav-text {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}
.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 20px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}
.nav-text a:hover {
  text-decoration: none;
}
.nav-menu-items {
  width: 100%;
}
.navbar-toggle {
  background-color: #343a40;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
span {
  margin-left: 16px;
}

.home {
  display: flex;
  height: 80vh;
  width: 100%;
  flex-direction: column;
}

.profile_wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  height: 30vh;
  background: #eef3f7;
  width: 80%;
  margin: 30px auto;
  border-radius: 13px;
}
.course_info {
  display: flex;
  flex-direction: column;
  width: 200px;
  padding: 20px;
  align-items: center;
  justify-content: center;
}
.prof_rep {
  display: flex;
  flex-direction: column;
  padding: 30px;
}
.prof_rep .image {
  background: lightblue;
  height: 300px;
  width: 300px;
  -webkit-shape-outside: circle();
          shape-outside: circle();
  -webkit-clip-path: circle();
          clip-path: circle();
}

.edu_prog {
  padding: 20px;
  display: flex;
  flex-direction: column;
  width: 200px;
  align-items: center;
  justify-content: center;
}

.chart_wrapper {
  background: #eef3f7;
  margin: auto;
  height: 50vh;
  width: 80%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.barchart {
  width: 30%;
  height: 90%;
}
.dough {
  width: 50%;
  height: 90%;
}
.progressbar-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.progress {
  width: 80%;
}
.assignment_container {
  width: 80%;
  margin: 30px auto;
  background: #eef3f7;
  border-radius: 10px;
}
.footer {
  background: #343a40;
  margin-top: 20px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer-text {
  color: #ffff;
  font-size: 1rem;
}

.body {
  z-index: -1;
}
.courseHeader {
  display: flex;
  justify-content: center;
  align-items: center;
}
.courseItems {
  width: 100vh;
  -webkit-column-count: 3;
          column-count: 3;
  margin: 10vh auto;
  display: flex;
  justify-content: space-between;
}
.courseContainer {
  border: 0.5px solid black;
  display: flex;
  flex-direction: column;
  width: 30vh;
}

.courseInfo {
  display: flex;
  flex-direction: row;
}

