.navbar {
  background-color: #343a40;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.dropdown {
  list-style: none;
  margin-right: 50px;
}
.dropdown a {
  color: #ffff;
  font-size: 18px;
}
.dropdown a:hover {
  color: #ffff;
}
.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
}
.nav-menu {
  background-color: #343a40;
  width: 250px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
  z-index: 100;
}
.nav-menu.active {
  left: 0;
  transition: 350ms;
}
.nav-text {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}
.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 20px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}
.nav-text a:hover {
  text-decoration: none;
}
.nav-menu-items {
  width: 100%;
}
.navbar-toggle {
  background-color: #343a40;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
span {
  margin-left: 16px;
}
